html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  color: #fff;
}

body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  background-color: #f00;
  min-height: 300px;
  flex-basis: 300px;
}

.block--1 {
  background-color: #f00;
}

.block--2 {
  background-color: #c00;
}

.block--3 {
  background-color: #900;
  order: 2;
}

.block--4 {
  background-color: #600;
  order: 1;
}

.block--5 {
  background-color: #300;
  order: 3;
}

.block--6 {
  background-color: #000;
  order: 4;
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33%;
  }
  .block--4 {
    order: 6;
  }
  .block--5 {
    order: 5;
  }
  .block--6 {
    order: 4;
  }
}

@media (max-width: 599px) {
  .block {
    order: 0;
  }
}
